import axios from 'axios'

const req = axios.create({
  // baseURL: 'https://tradmin.youdao.com',
  baseURL: 'http://ws075x.corp.youdao.com:19960',
  timeout: 5000,
  withCredentials: true,
});

// https://gitlab.corp.youdao.com/course/teachingresearchserver/blob/master/README.md

// 
export function getUserStatus() {
  return req.get('/my/info')
}

// 
export function getMyCourses() {
  return req.get('/my/courses')
}

// 
export function getHomeworkList(courseId) {
  return req.get('/homework/list', {
    params: {
      courseId,
    },
  })
}

// 
export function getBookQuestionByPage(page, bookId) {
  return req.get('/book/questions', {
    params: {
      page,
      id: bookId,
    },
  })
}

// 
//  type, id, start, end, index, studentId
export function getHomeworkPreview(filterData) {
  return req.get('/homework/preview', {
    params: filterData,
  })
}

// 
//  type, id, start, end, index, studentId
export function getHomework(queryString) {
  return req.get(`/homework/pick${queryString}`)
}

// 
export function correctHomework(data) {
  return req.post('/homework/correct', data)
}

// 
export function saveHomework(data) {
  return req.post('/homework/save', data)
}

// 
export function getQuizzes(params) {
  return req.get('/quiz/list', {
    params,
  })
}

// 
export function getQuizzesForStudent(params) {
  return req.get('/quiz/student', {
    params,
  })
}

// 
export function postQuizzesReadAll(params) {
  return req.post('/quiz/readAll', params)
}

// 
export function getQuizPreview(params) {
  return req.get('/quiz/preview', {
    params,
  })
}

// 
export function postQuizCorrect(params) {
  return req.post('/quiz/correct', params)
}




